

const todos = (state = {todos:[]}, action) => {

  console.log(action);
  
    switch (action.type) {
      case 'LOAD_TODOS':
        return {todos:action.todos};
      case 'ADD_TODO':
        return [
          ...state,
          {
            id: action.id,
            text: action.text,
            completed: false
          }
        ]
      case 'TOGGLE_TODO':
        return state.map(todo =>
          todo.id === action.id ? { ...todo, completed: !todo.completed } : todo
        )
      default:
        return state
    }
  }
  
  export default todos